.cart {
  background-color: var(--color-primary-900);
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.cart .content {
  flex-direction: column;
  flex: 1;
  align-items: center;
  padding: 20px;
  display: flex;
}

.cart .content h1 {
  color: #fff;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  font-size: 2em;
  display: flex;
}

.cart .content h1 svg {
  transform: translateY(2px);
}

.cart .content h2 {
  font-size: 26px;
}

.cart .content-items {
  background-color: var(--color-primary-700);
  grid-template-columns: 1fr;
  gap: 20px;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  display: grid;
}

@media only screen and (width >= 600px) {
  .cart .content-items {
    grid-template-columns: repeat(2, 1fr);
    max-width: 700px;
  }
}

@media only screen and (width >= 1024px) {
  .cart .content-items {
    grid-template-columns: repeat(3, 1fr);
    max-width: 900px;
  }
}

@media only screen and (width >= 1280px) {
  .cart .content-items {
    max-width: 1100px;
  }
}

@media only screen and (width >= 1440px) {
  .cart .content-items {
    max-width: 1300px;
  }
}

.cart .content-items-item {
  background-color: var(--color-primary-500);
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.cart .content-items-item .item-image {
  aspect-ratio: unset;
  width: 100%;
  height: auto;
  margin-top: 16px;
}

@media only screen and (width >= 600px) {
  .cart .content-items-item .item-image {
    aspect-ratio: 3 / 4;
  }
}

.cart .content-items-item h3 {
  text-align: center;
  margin: 0 0 10px;
}

.cart .content-items-item p {
  margin: 5px 0;
}

.cart .content-items-item button {
  background-color: var(--color-primary-200);
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 8px 16px;
  transition: background-color .3s;
}

.cart .content-items-item button:hover {
  background-color: var(--color-primary-300);
}

.cart .content .empty-cart-message {
  text-align: center;
  color: #fff;
  background-color: var(--color-primary-700);
  border-radius: 8px;
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  font-size: 1.2em;
  box-shadow: 0 2px 4px #0000001a;
}

.cart .content .empty-cart-section {
  text-align: center;
  width: 100%;
  max-width: 600px;
  margin-top: 20px;
}

.cart .content .empty-cart-btn {
  background-color: var(--color-error-60);
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.1em;
  transition: background-color .2s;
}

.cart .content .empty-cart-btn:hover {
  background-color: var(--color-error-30);
}

.cart .content .empty-cart-confirmation {
  background-color: var(--color-error-20);
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.cart .content .empty-cart-confirmation p {
  color: #fff;
  margin-bottom: 15px;
}

.cart .content .empty-cart-confirmation .confirmation-buttons {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.cart .content .empty-cart-confirmation .confirmation-buttons button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 1em;
  transition: background-color .2s;
}

.cart .content .empty-cart-confirmation .confirmation-buttons .confirm-btn {
  background-color: var(--color-error-30);
  color: #fff;
}

.cart .content .empty-cart-confirmation .confirmation-buttons .confirm-btn:hover {
  background-color: var(--color-error-60);
}

.cart .content .empty-cart-confirmation .confirmation-buttons .cancel-btn {
  background-color: var(--color-primary-500);
  color: #fff;
}

.cart .content .empty-cart-confirmation .confirmation-buttons .cancel-btn:hover {
  background-color: var(--color-primary-600);
}

.cart .cart-summary-container {
  background-color: var(--color-primary-700);
  border-radius: 24px;
  width: 100%;
  max-width: 660px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 30px;
  box-shadow: 0 4px 6px #0000001a;
}

@media only screen and (width >= 600px) {
  .cart .cart-summary-container {
    padding: 40px;
  }
}

.cart .cart-summary {
  text-align: center;
}

.cart .cart-summary h2 {
  color: #fff;
  margin-bottom: 20px;
}

.cart .cart-summary .order-form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.cart .cart-summary .order-form .form-group {
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
  display: flex;
}

.cart .cart-summary .order-form .form-group label {
  color: #fff;
  font-size: 1.1em;
}

.cart .cart-summary .order-form .form-group input, .cart .cart-summary .order-form .form-group select {
  background-color: var(--color-primary-900);
  color: #fff;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 8px;
  font-size: 1em;
}

.cart .cart-summary .order-form .form-group input:focus, .cart .cart-summary .order-form .form-group select:focus {
  outline: 2px solid var(--color-primary-500);
}

.cart .cart-summary .order-form .send-order-btn {
  background-color: var(--color-primary-500);
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1.1em;
  transition: background-color .3s;
}

.cart .cart-summary .order-form .send-order-btn:hover {
  background-color: var(--color-primary-600);
}
